.card-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 75vh;
    width: 25rem;
    padding: 20px;
}

.card {
    height: 80px;
    perspective: 200px;
}

.card-inner {
    width: 100%;
    height: 100%;
    transition: transform 1s;
    transform-style: preserve-3d;
}

.card.flipped .card-inner {
    transform: rotateY(180deg);
}

.card-front,
.card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    border-radius: 10px;
}

.card-front {
    background-color: white;
}

.card-back {
    background: rgb(56, 161, 105);
    background: linear-gradient(45deg, rgba(56, 161, 105, 1) 33%, rgba(238, 238, 238, 1) 100%);
    transform: rotateY(180deg);
}